import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;



import uniIcons from '../../components/uni-icons/uni-icons.vue';
export default {
	components: {
		uniIcons,
	},
	data() {
		return {
			hShare: false,
			userList: [{
					icon: 'icon-08_zizhanghaoguanli',
					name: '账号管理',
					color: '#3DABFF',
					url: 'accountmange'
				},
				{
					icon: 'icon-fapiao',
					name: '开具发票',
					color: '#FF9C00',
					url: 'receipt'
				},
				{
					icon: 'icon-tianchongxing--',
					name: '账单记录',
					color: '#FFF000',
					url: 'bill'
				},
				{
					color: '#ffab9a',
					icon: 'icon-buy-fill',
					name: '购买记录',
					url: 'purchase_record'
				},
				{
					icon: 'icon-qianbao',
					name: '充值',
					color: '#2DC4B4',
					url: 'invest'
				},
				{
					icon: 'icon-juan',
					name: '现金劵',
					color: '#F8632F',
					url: 'cash'
				},

				{
					icon: 'icon-weixin',
					name: uni.getStorageSync('is_miniBind') == 0 ? '绑定微信' : '改绑微信',
					color: '#26DD5B',
					url: 'bindWeChat'
				}



			],
			is_bind: '',
			is_child: '',
			token: '',
			imgRemote: imgRemote,
			memberInfoData: '',
			member_default: '',
			code: '',
			shareInfo:[],



		};
	},
	methods: {
		share() {
			if (!this.token) {
				uni.navigateTo({
					url: '/pages/account/login'
				})
			} else {
				if (this.hShare == false) {
					let that = this;
					that.hShare = true;
					var timeStamp = Math.round(new Date().getTime() / 1000);
					var obj = {
						appid: appid,
						timeStamp: timeStamp,
					}
					var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
					var data = {
						appid: appid,
						timeStamp: timeStamp,
						sign: sign,
					}
					rs.getRequests("shareConfig", data, (res) => {
						if (res.data.code == 200) {
							var data = res.data.data;
							WeixinJSBridge.on('menu:share:appmessage', function(argv) {
								WeixinJSBridge.invoke('sendAppMessage', {
									"appid": that.userinfo.appId, //appid 设置空就好了。
									"img_url": data.share_img, //分享时所带的图片路径
									"img_width": "120", //图片宽度
									"img_height": "120", //图片高度
									"link": data.share_href, //分享附带链接地址
									"desc": data.share_describe, //分享内容介绍
									"title": data.share_title
								}, function(res) { /*** 回调函数，最好设置为空 ***/
									this.hShare = false
									console.log(res)
								});

							});
						} else {
							rs.Toast(res.data.msg)
						}
					})
				} else {
					this.hShare = false
				}

			}
		},
		memberInfo() {
			var that = this
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.getRequests("memberInfo", data, (res) => {
				// console.log(res)
				if (res.data.code == 200) {
					this.memberInfoData = res.data.data.info;
					this.member_default = res.data.data.member_default;
				}
			})
		},
		myinfoPage() {
			if (!this.token) {
				uni.navigateTo({
					url: '/pages/account/login'
				})
			} else {
				uni.navigateTo({
					url: "/pages/user/myinfo"
				})
			}

		},
		pageUrl(item) {
			if (this.token) {
				if (item.url == 'bindWeChat') {










					this.wxbindWeChat()


				} else {
					getApp().globalData.isReload = true;
					uni.navigateTo({
						url: `/pages/user/${item.url}`
					});
				}
			} else {
				uni.reLaunch({
					url: '/pages/account/login'
				});
			}
		},

		//小程序绑定
		wxbindWeChat(e) {
			console.log("小程序绑定")
			var that = this;
			uni.showModal({
				content: this.is_bind == 1 ? '是否微信改绑' : '是否绑定微信',
				cancelText: "我再想想",
				cancelColor: "#999",
				confirmText: "确认",
				confirmColor: "#009a44",
				success: function(res) {
					if (res.confirm) {
						uni.getUserInfo({
							provider: 'weixin',
							success(infoRes) {
								let {
									encryptedData,
									iv
								} = infoRes;
								console.log(infoRes);
								uni.login({
									provider: 'weixin',
									success(res) {
										console.log(res.code)
										that.wxbindWeChata(res.code)
									}

								})
							}
						})
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});

		},
		wxbindWeChata(code) {
			var that = this;
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				code: code,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				code: code,
				type: "mini",
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.postRequests("bindWeChat", data, (res) => {
				if (res.data.code == 200) {
					rs.Toast('绑定微信成功');

					setTimeout(function() {
						uni.clearStorage({
							success: function(reg) {
								uni.navigateTo({
									url: '/pages/account/login'
								});
							}
						})
					}, 1000);
					// uni.setStorageSync('is_miniBind', 1)
					// that.is_bind = uni.getStorageSync("is_miniBind")
				} else {
					rs.Toast(res.data.msg)
				}
			})
		},
		// h5绑定微信
		adminisus_weixin() {
			console.log("H5绑定")
			var that = this;
			uni.showModal({
				content: this.is_bind == 1 ? '是否微信改绑' : '是否绑定微信',
				cancelText: "我再想想",
				cancelColor: "#999",
				confirmText: "确认",
				confirmColor: "#DEC17C",
				success: function(res) {
					if (res.confirm) {
						uni.setStorageSync('isWeixin', true)
						let code = location.search;
						let getCode = code.substring(code.indexOf('=') + 1, code.lastIndexOf('&'));
						if (!getCode) {
							let url = window.location.href;
							let redirect_uri = encodeURIComponent(url);
							let a = "https://open.weixin.qq.com/connect/oauth2/authorize?" +
								"appid=" + that.userinfo.appId + "&redirect_uri=" + redirect_uri +
								"&response_type=code&scope=snsapi_userinfo#wechat_redirect"
							window.location.href = a;
						}
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
		},
		//APP绑定
		bindWeChat() {
			console.log("APP绑定")
			var that = this
			uni.getProvider({
				service: 'oauth',
				success: function(res) {
					//res.provider  检测手机上是否安装微信、QQ、新浪微博等
					if (~res.provider.indexOf('weixin')) {
						//手机安装了微信
						uni.login({
							provider: 'weixin',
							success: function(loginRes) {
								// console.log('-------获取openid(unionid)-----');
								// console.log(JSON.stringify(loginRes));
								uni.getUserInfo({
									provider: 'weixin',
									success: function(infoRes) {
										that.code = infoRes.userInfo.openId;
										that.bindWeChata()
									}
								});

							}
						});
					} else {
						uni.showToast({
							title: '手机上还没安装微信,请安装微信后重试',
							duration: 2000,
							icon: "none"
						});
					}

				}
			});
		},
		bindWeChata() {
			var that = this;
			uni.showModal({
				content: this.is_bind == 1 ? '是否微信改绑' : '是否绑定微信',
				cancelText: "我再想想",
				cancelColor: "#999",
				confirmText: "确认",
				confirmColor: "#DEC17C",
				success: function(res) {
					if (res.confirm) {
						// console.log('用户点击确定');
						var app_openid = that.code;
						var timeStamp = Math.round(new Date().getTime() / 1000);
						var obj = {
							appid: appid,
							timeStamp: timeStamp,
						}
						var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
						var data = {
							appid: appid,
							app_openid: app_openid,
							type: "app",
							timeStamp: timeStamp,
							sign: sign,
						}
						rs.postRequests("saveMemberInfo", data, (res) => {
							// console.log(res)
							if (res.data.code == 200) {
								rs.Toast('绑定微信成功,')
								setTimeout(function() {
									uni.clearStorage({
										success: function(reg) {
											uni.navigateTo({
												url: '/pages/account/login'
											});
										}
									})
								}, 1000);
								// uni.setStorageSync('is_miniBind', 1)
								// that.is_bind = uni.getStorageSync("is_miniBind")
							} else {
								rs.Toast(res.data.msg)
							}
						})
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
		},
		threePage(data) {
			if (this.token) {
				switch (data) {
					case 'recomend':
						uni.navigateTo({
							url: '/pages/index/recommed'
						});
						break;
					case 'address':
						uni.navigateTo({
							url: '/pages/user/userAddress'
						});
						break;
					case 'password':
						uni.navigateTo({
							url: '/pages/user/modifypwd'
						});
						break;
				}
			} else {
				uni.reLaunch({
					url: '/pages/account/login'
				});
			}
		},
		exit() {
			if (!this.token) {
				uni.navigateTo({
					url: '/pages/account/login'
				});
				return;
			}
			uni.showModal({
				title: '提示',
				content: '是否退出登录？',
				success: function(res) {
					if (res.confirm) {
						// console.log('用户点击确定');
						var that = this
						var timeStamp = Math.round(new Date().getTime() / 1000);
						var obj = {
							appid: appid,
							timeStamp: timeStamp,
						}
						var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
						var data = {
							appid: appid,
							timeStamp: timeStamp,
							sign: sign,
						}
						rs.getRequests("logout", data, (res) => {
							if (res.data.code == 200) {
								rs.Toast( "退出成功");
								
								setTimeout(function() {
									uni.clearStorage({
										success: function(reg) {
											uni.navigateTo({
												url: '/pages/account/login'
											});
										}
									})
								}, 1000);
							}
						})
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
		},
		wxConfig() {

			if (this.token) {

				var that = this
				var timeStamp = Math.round(new Date().getTime() / 1000);
				var obj = {
					appid: appid,
					timeStamp: timeStamp,
				}
				var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
				var data = {
					appid: appid,
					timeStamp: timeStamp,
					sign: sign,
				}
				rs.getRequests("wxConfig", data, (res) => {
					if (res.data.code == 200) {
						this.userinfo = res.data.data;
						wx.config({
							debug: false, // 开启调试模式
							appId: res.data.data.appId, // 必填，公众号的唯一标识
							timestamp: res.data.data.timestamp, // 必填，生成签名的时间戳
							nonceStr: res.data.data.nonceStr, // 必填，生成签名的随机串
							signature: res.data.data.signature, // 必填，签名，见附录1
							jsApiList: [
								'updateAppMessageShareData', 'onMenuShareAppMessage'
							] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
						});
					}
				})
			}

		}


	},
	onLoad() {
		uni.hideTabBar();
	},
	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow: function() {
		var that = this;
		that.memberInfo();
		that.is_bind = uni.getStorageSync('is_miniBind');
		that.is_child = uni.getStorageSync("is_child");

		that.token = uni.getStorageSync("cdj_token");
		var timeStamp = Math.round(new Date().getTime() / 1000);
		var obj = {
			appid: appid,
			timeStamp: timeStamp,
		}
		var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
		var data = {
			appid: appid,
			timeStamp: timeStamp,
			sign: sign,
		}
		rs.getRequests("shareConfig", data, (res) => {
			if(res.data.code==200){
				that.shareInfo=res.data.data;
			
			}
		})
		
		//H5











































	},
	// 分享
	onShareAppMessage() {
				let path=this.shareInfo.share_href;	
		return {
			"imageUrl": this.shareInfo.share_img, //分享时所带的图片路径
			"path": path.substring(path.indexOf('#')+1), //分享附带链接地址
			"desc": this.shareInfo.share_describe, //分享内容介绍
			"title": this.shareInfo.share_title
		}
	}
};
